import React from "react";
import Button from '@material-ui/core/Button';

import shuffleArray from 'shuffle-array';
import cloneDeep from 'clone-deep';

const isValidPosition = (position, field_size) =>
  position.x >= 0
    && position.x <= field_size.width - 1
    && position.y >= 0
    && position.y <= field_size.height - 1;

const getPositionFromIndex = (index, field_size) => {
  const position = {
    x: index % field_size.width,
    y: Math.floor(index / field_size.width),
  };

  return (
    isValidPosition(position, field_size)
      ? position
      : null
  );
};

const getIndexFromPosition = (position, field_size) =>
  isValidPosition(position, field_size)
    ? position.x + position.y * field_size.width
    : null;

const getAroundCellIndex = (index, field_size) => {
  const position = getPositionFromIndex(index, field_size);

  return [
    {x: position.x - 1, y: position.y - 1},
    {x: position.x - 1, y: position.y    },
    {x: position.x - 1, y: position.y + 1},
    {x: position.x    , y: position.y - 1},
    {x: position.x    , y: position.y + 1},
    {x: position.x + 1, y: position.y - 1},
    {x: position.x + 1, y: position.y    },
    {x: position.x + 1, y: position.y + 1},
  ]
    .filter(x => isValidPosition(x, field_size))
    .map(x => getIndexFromPosition(x, field_size));
};

const getAroundMineNum = (index, field, field_size) =>
  getAroundCellIndex(index, field_size)
    .map(x => field[x].is_mine ? 1 : 0)
    .reduce((s, x) => s + x);

const getInitialField = (field_size, mine_num) => {
  let field =
    [...Array(field_size.width * field_size.height).keys()]
      .map(i => ({
        is_mine: i < mine_num,
        opened: false,
        flagged: false,
        around_mine_num: null,
      }));

  shuffleArray(field);

  return field
    .map((x, i) => ({
      ...x,
      around_mine_num: getAroundMineNum(i, field, field_size),
    }));
};

const openCellRecursive = (index, field, field_size) => {
  if(field[index].flagged) return field;

  field[index].opened = true;

  if(field[index].around_mine_num === 0 && !field[index].is_mine){
    const open_targets =
      getAroundCellIndex(index, field_size)
        .filter(x => !field[x].opened && !field[x].flagged);

    open_targets.forEach(x => {
      field = openCellRecursive(x, cloneDeep(field), field_size);
    });
  }

  return field;
}

class MineSweeper extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      field: getInitialField(props.field_size, props.mine_num),
    };
  }

  toggleFlag(index){
    if(this.state.field[index].opened) return;

    const field = cloneDeep(this.state.field);
    field[index].flagged = !field[index].flagged;

    this.setState({field});
  }

  openCell(index){
    this.setState({field: openCellRecursive(index, cloneDeep(this.state.field), this.props.field_size)});
  }

  render() {
    const button_size_px = 40;
    const is_gameover =
      this.state.field
        .filter(x => x.is_mine && x.opened)
        .length !== 0;
    const is_game_clear =
      this.state.field
        .filter(x => !x.is_mine && !x.opened)
        .length === 0;
    const flag_num =
      this.state.field
        .filter(x => x.flagged)
        .length;

    const buttons =
      this.state.field
        .map((x, i) =>
          <Button
            onClick={e => !(is_game_clear || is_gameover) && this.openCell(i)}
            onContextMenu={e => {
              this.toggleFlag(i);
              e.preventDefault();
            }}
            style={{
              width: button_size_px,
              minWidth: button_size_px,
              height: button_size_px,
              padding: 0,
            }}
            variant={x.opened ? "text" : "contained"}
            disabled={x.opened}
            key={i}
          >
            {
              (() => {
                if(x.flagged) return "üö©";
                if(!x.opened) return "";
                if(x.is_mine) return "üí£";
                if(x.around_mine_num === 0) return "";
                return x.around_mine_num;
              })()
            }
          </Button>
        );

    return (
      <div>
        <p>ÊÆã„Çä„ÅÆüí£Ôºö{this.props.mine_num - flag_num}ÂÄã</p>
        {
          is_game_clear
            ? <p>„Ç≤„Éº„É†„ÇØ„É™„Ç¢ÔºÅ„Åä„ÇÅ„Åß„Å®„ÅÜÔºÅ</p>
            : null
        }
        {
          is_gameover
            ? <p>„Ç≤„Éº„É†„Ç™„Éº„Éê„ÉºÔºÅÊÆãÂøµÔºÅ</p>
            : null
        }
        <div
          style={{
            width: button_size_px * this.props.field_size.width,
          }}
        >
          {buttons}
        </div>
      </div>
    );
  }
}

export default MineSweeper;

