import React from "react";
import Button from '@material-ui/core/Button';

class AnimalCounter extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      count: 0,
    };
  }

  render() {
    const unit_prefix =
      this.state.count === 0
        ? "„Å≤"
        : ["„Å¥", "„Å¥", "„Å≤", "„Å≥", "„Å≤", "„Å≤", "„Å¥", "„Å≤", "„Å¥", "„Å≤"][this.state.count % 10];
    const unit = `${unit_prefix}„Åç`;

    return (
      <Button
        variant="contained"
        onClick={() => {
          this.setState({
            count: this.state.count + 1,
          })
        }}
      >
        {`${this.props.animal_name}„Åå${this.state.count}${unit}`}
      </Button>
    );
  }
}

export default AnimalCounter;

